
import XHeader from 'vux/src/components/x-header/index.vue'


export default{
name:'aboutus',
data(){
return{
article_data:'',
}
},
components:{
XHeader
},methods:{
get_article(id){
this.$axios.post("/api/Article/get_article_detail",`data=${JSON.stringify({
article_id: id
})}`).then(results=>{
if(results.code==200){
this.article_data = results.data.content
}
}).catch(err=>{

});
},
},
mounted(){
this.get_article(84);
}
}



